var util = {}
//
util.isLocalhost = function(){
	var k_host = window.location.host;
	if(k_host=="http://localhost/"){
		return true;
	}else{
		return false;
    }
}
//
util.msg = function(msg){
	return $("body").attr("data-id",1);
}


export default{
  install(Vue,options){
  	 Vue.prototype.utilIsLocalhost = function(){
  	 	var k_host = window.location.host;
		if(k_host=="http://localhost/"){
			return true;
		}else{
			return false;
	    }
  	 };
  	  Vue.prototype.utilMsg = function(msgs){
  	  	var msgText = $("#msgText").text();
  	  	if(!msgText){
  	  		var html = '<div id="msgText">'+msgs+'</div>'
  	  		$("body").append(html);
  	  	}else{
  	  		$("#msgText").text(msgs);
  	  	}
  	  	$("#msgText").fadeIn();
  	  	setTimeout(function(){
  	  		$("#msgText").fadeOut();
  	  	},3000)
  	 };
  }
}


