<template>
  <div class="contentHtml" style="margin-bottom: 50px;">
  	<div class="detailBox">
	  	<div class="title">{{title}}</div>
	  	<div class="idInfo">
	  		<img class="fl imgBr" src="../assets/img.png" width="40" height="40"/>
	  		<span class="fl" style="margin-left: 10px;"><br/></span>
	  		<span class="follow" v-on:click="follow"></span>
	  	</div>
	  	<div class="content"></div>
	  	<div style="color: #999;">3333<span v-on:click="fabulous('post',datas.id)" class="fabulous">211</span></div>
  	</div>
  	<div class="initBox shareBox" style="margin-bottom: 10px;">
	  	<div class="font30 font30 subTite"></div>
	  	<div class="iconBox">
	  		<img v-on:click="share('weixin_circle')" src="../assets/pengyouquan@3x.png" width="44" height="44" />
	  		<img v-on:click="share('weixin_friend')" src="../assets/weixinhaoyou@3x.png" width="44" height="44" />
	  		<img v-on:click="share('weibo')" src="../assets/weibo@3x.png" width="44" height="44" />
	  	</div>
  	</div>
  	<div class="initBox">
  		<div class="font30 subTite"></div>
  	</div>
  	<ul class="listBox" style="margin-top: 0;">
  		<template v-for="listData in listDatas">
    		<li v-on:click="hrefTo(listData.id)">
    			<div class="leftBox">
    				<div class="listTitle">{{listData.title.rendered}}</div>
    				<div class="listInfo"><span class="but1"></span><span> {{listData.date | timeFormatter}} {{listData.id}}</span></div>
    			</div>
    			<div class="rightBox"><img src="../assets/img.png"></div>
    		</li>
    		</template>
    	</ul>
    	<div class="initBox">
  		<div class="subTite font30"></div>
  		<ul class="commintBox">
  			<li v-for="comments in commentList">
  				<div class="authorInfo">
  					<img class="fl imgBr" v-bind:src="comments.author_avatar_urls[48]" width="30" height="30" style="margin-right: 10px;"/>
  					<span class="fl"> {{comments.author_name}}<br/> </span>
  					<span v-on:click="fabulous('comment',comments.id)" class="fabulous">211</span>
  				</div>
  				<div class="text"></div>
  			</li>
  		</ul>
    	</div>
  	<div class="bottomNav">
  		<input v-model="value" type="text" placeholder="" v-on:change="change" />
  		<img src="../assets/pinglun@3x.png" width="20" height="20"/>
  		<img src="../assets/shoucang@3x.png" width="20" height="20"/>
  		<img src="../assets/dadianzan@3x.png" v-on:click="fabulous('post',datas.id)" width="20" height="20"/>
  	</div>
  </div>
</template>
<script type="text/javascript">
 $("title").text("");
export default {
  name: 'contentHtml',
  data () {
    return {
    		datas:'',
    		title:'',
    		content:'',
    		listDatas:[],
    		value:'',
    		commentList:[],
    		commentPage:1
    }
  },
  created:function(){
  	var _self=this;
  	var id = _self.$route.params.id
  	$.get('http://121.14.27.27:88/wp-json/wp/v2/posts/'+id,{},function(data){
  		console.log(data)
  		_self.datas = data;
		_self.title = data.title.rendered;
		$(".content").html(data.content.rendered);
	})
  	
  	$.get('http://121.14.27.27:88/wp-json/wp/v2/posts',{},function(data){
  		console.log(data)
		_self.listDatas = data;
	})
  	
  	
  	
  	 //
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //
	  var windowH = $(window).height(); //
	  var height = $(document).height();  //
	  if(top+windowH==height){
	  	var addHtml = $(".addHtml").text();
	  	var endHtml = $(".endHtml").text();
	  	if(addHtml||endHtml){
	  		return false;
	  	}else{
	  		$('html,body').animate({scrollTop:top-10},0);
	  		getList()
	  		return false;
	  	}
	  }
	})
	getList();
	function getList(){
		$(".commintBox").append('<li class="addHtml" style="text-align:center;">  </li>')
		var page = _self.commentPage;
    		var per_page = 4;
		var data = {"page":page,"per_page":per_page}
		$.get('http://121.14.27.27:88/wp-json/wp/v2/comments',data,function(res){
			$(".addHtml").remove();
			var start = _self.commentList.length;
			for(var i = 0; i < res.length; i++) {
		      _self.commentList[start+i] = res[i];
		    }
			console.info(_self.commentList)
			 _self.$forceUpdate();
			_self.commentPage = _self.commentPage+1;
			if(res.length < per_page){
				var lengths = $(".commintBox li").length;
				if(lengths==_self.commentList.length){
				$(".commintBox").append('<li class="endHtml" style="text-align: center;">  </li>')
				}
			}
		},'json')
	}
	
	
  },
  methods: {
    follow: function (event) {
    		alert('')
    },
    scroll: function (event) {
    		alert('scroll')
    },
    fabulous: function (type,id) {
	    	var user = JSON.parse(localStorage.user);
	    	var token =  user.token;
    		var _this = this;
    		var status = 'like';
    		if(type=="post"){
    			var post_id = id;
	    		var datas = {"post_id":post_id,"status":status}//status=like or unlike
    			var ajaxUrl = 'http://121.14.27.27:88/wp-json/ulike/v1/posts';
    		}else{
	    		var comment_id = id;
	    		var datas = {"comment_id":comment_id,"status":status}//status=like or unlike
    			var ajaxUrl = 'http://121.14.27.27:88/wp-json/ulike/v1/comments';
    		}
    		$.ajax({
    			type:'post',
    			data: datas,
    			async: false,
			url: ajaxUrl,
			beforeSend: function(xhr) {
    				xhr.setRequestHeader('Authorization', 'Bearer '+token);
            },
			success: function(data){
				_this.utilMsg('')
				//data
			},
            error: function(data){
            		console.log(JSON.parse(data.responseText))
            		_this.utilMsg(JSON.parse(data.responseText).message)
            }
		});
    },
    //
    share: function (type) {
    		var title = this.datas.title.rendered;
    		var info = this.datas.slug;
    		var img = this.datas.slug;
    		var id = this.datas.id;
    		var content = {"type":type,"title":title,"info":info,"img":img,"id":id}
        	var data = JSON.stringify(content);
        	console.log(data)
      	if(!this.utilIsLocalhost){
			ttf.nslog(data)
	  	}
    },
	hrefTo:function(id){//ios
	  	var content = {"type":"href","id":id}
	    	var data = JSON.stringify(content);
	    	if(!this.utilIsLocalhost){
			ttf.nslog(data)
	  	}
	},
    change: function () {
    	    var _this = this;
    		var user = JSON.parse(localStorage.user);
    		var content = this.value;
    		var post = this.$route.params.id
    		var token =  user.token;
		var datas = {"content":content,"post":post}
    		console.log(datas)
    		$.ajax({
    			type:'post',
    			data: datas,
    			async: false,
			url: 'http://121.14.27.27:88/wp-json/wp/v2/comments',
			beforeSend: function(xhr) {
    				xhr.setRequestHeader('Authorization', 'Bearer '+token);
            },
			success: function(data){
				$(".bottomNav input").val("");
				//data
			},
            error: function(data){
            		console.log(JSON.parse(data.responseText))
            		_this.utilMsg(JSON.parse(data.responseText).message)
            }
		});
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	.subTite{line-height:50px;border-bottom: 1px solid #f7f7f7;text-align: center;}
	.detailBox{background: #fff;margin-bottom: 10px;padding: 10px;}
	.detailBox .title{font-size: 2rem;color: #333;}
	.detailBox .idInfo{overflow: hidden;margin: 30px 0;}
	.detailBox .idInfo .follow{float: right;padding: 6px 10px;color: #ff1919;border: 1px solid #ff1919;border-radius: 5px;-webkit-border-radius: 5px;}
	.detailBox .content{font-size: 1rem;color: #666;}
	.detailBox .fabulous{float: right;background: url(../assets/dadianzan@3x.png)left no-repeat;background-size: 1rem 1rem;padding-left:1.5rem;}
	.shareBox .title{line-height:50px;border-bottom: 1px solid #f7f7f7;}
	.shareBox .iconBox{display:flex;display:-webkit-flex;justify-content:space-around;padding: 10px 0;}
	.commintBox .fabulous{float: right;background: url(../assets/dadianzan@3x.png)left no-repeat;background-size: 1rem 1rem;padding-left:1.5rem;}
	.commintBox li{padding: 10px 0;border-bottom: 1px solid #f7f7f7;}
	.commintBox li .authorInfo{color: #999;overflow: hidden;margin-bottom: 10px;}
	.commintBox li .text{margin-left:40px ;font-size: 1.5rem;}
	.bottomNav{
		height:30px;position: fixed;bottom: 0;left: ;width: 100%;
		background: #f7f7f7;display:flex;display:-webkit-flex;
		justify-content:space-around;padding:10px 0;
	}
	.bottomNav input{
		width:17rem;line-height: 2rem;height: 2rem;
		border-radius:100px;-webkit-border-radius:100px;
	}
</style>



