<template>
  <div class="indexHtml">
   <br/>
   <router-link to="/information"></router-link>
   <br/>
   <router-link to="/information/37"></router-link>
   <br />
   <router-link to="/post/add"></router-link>
   
  </div>
</template>

<script>

export default {
  name: 'indexHtml',
  data () {
    return {
    	listDatas:'',
    	msg: '',
    	input:'',
    	message:''
    }
  },
  created:function(){
  	var _self=this;
  	$.get('http://121.14.27.27:88/wp-json/wp/v2/posts',{},function(data){
//		console.log(eval(data))
		_self.listDatas = eval(data);
	})
	var username = '15259641845';
	var password = '123456';
	var email = '576038240@qq.com';
	var data = {"username":username,"password":password,"email":email}
	//
//	$.get('http://121.14.27.27:88/wp-json/wp/v2/users/',data,function(data){
//		
//		console.log(data)
//	})
	//
	$.post('http://121.14.27.27:88/wp-json/jwt-auth/v1/token',data,function(data){
		console.log(data)
		localStorage.setItem("user",JSON.stringify(data))
	})
	
	
  }
  ,
  methods: {
    change: function (event) {
    		this.msg = $("p").text()
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	a{display: block;text-align:center;font-size: 2rem;}
</style>



