<template>
  <div class="addtHtml">
  	<div class="addList"><h3><span class="inputLeft">:</span><input v-model="title" type="" placeholder=""></h3></div>
  	<div class="addList">
  		<h3>:</h3>
  		<textarea v-model="excerpt" type="" placeholder=""></textarea>
  	</div>
  	<!--<div class="addList">
  		<h3></h3>
  		<textarea v-model="excerpt" type="" placeholder=""></textarea>
  	</div>-->
  	<div class="addList">
  		<h3>:</h3>
  		<textarea v-model="content" type="" style="height: 200px;" placeholder=""></textarea>
  	</div>
  	<div class="butAdd" v-on:click="add"></div>
  </div>
</template>

<script>

export default {
  name: 'addtHtml',
  data () {
    return {
    		content: '',
    		title:'',
    		excerpt: '',
    }
  },
  created:function(){
  	
  },
  methods: {
    add: function (event) {
    		var user = JSON.parse(localStorage.user);
    		var author = user.id;
    		var token =  user.token;
    		var content = this.content;
    		var title = this.content;
    		if(!title){
    			alert("");
    			return false;
    		}else if(!content){
    			alert("");
    			return false;
    		}
    		var datas = {"content":content,"title":title,"author":author,"excerpt":this.excerpt}
    		$.ajax({
    			type:'post',
    			data: datas,
    			async: false,
			url: 'http://121.14.27.27:88/wp-json/wp/v2/posts',
			beforeSend: function(xhr) {
    				xhr.setRequestHeader('Authorization', 'Bearer '+token);
//  				xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
            },
			success: function(res){
//				alert("");
			debugger
				 console.log(res)
				//data
			},
            error: function(res){
            	 alert("");
              console.log('')
              console.log(res)
            }
		});
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.addList{margin-bottom: 10px;font-size: 2rem;}
	.addList textarea{width:96%;font-size: 1.5rem; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px;margin: 0;}
	.addList input{border: 0;font-size: 1.5rem;width: 75%;}
	.addList .inputLeft{width: 25%;}
	.butAdd{width: 80%;margin: 0 auto; background: #ff1919;color: #fff;font-size: 1.5rem;text-align: center;line-height: 4rem;}
</style>



