<template>
  <div id="app">
    <app-header></app-header>
    <app-nav></app-nav>
    <router-view/>
    <app-footer></app-footer>
  </div>
</template>
<script>
//
setFont();
$(window).resize(function(){
	setFont();
})
function setFont(){
	var winWidth = $('body').width();
	if(winWidth<769){
		$("html,body").css("font-size",20*(winWidth/640)+"px");
	}
}
export default {
  name: 'app'
}
</script>

<style>
body,html,ul,li,a,dl,dd,dt,img,a{padding: 0;margin: 0;border: 0;}
body,html{max-width: 768px;margin: 0 auto;background: #f7f7f7;}
h1, h2 ,h3,h4,h5,h6{font-weight: normal;}
h3{font-size: 2rem;}
ul {list-style-type: none;}
a {color: #42b983;text-decoration: none;}
::-webkit-input-placeholder{color:#999;}
input{
	-webkit-appearance:none;
	appearance:none;outline:none;
	-webkit-tap-highlight-color:rgba(0,0,0,0);
	border-radius:0;
	border: 1px solid #eee;
	padding: 4px 8px;
}
.font30{font-size: 2rem;}
.text-c{text-align:center;}
.initBox{background: #fff;padding: 10px;}
.fl{float: left;}
.fr{float: right;}
.oh{overflow: hidden;}
.imgBr{border-radius: 50%;-webkit-border-radius: 50%;}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
  font-size: 0.8rem;
}

.line{border-bottom: 1px solid #ebebeb;}
#msgText{
	background: rgba(0,0,0,0.7);padding:6px 10px;border-radius:3px;-webkit-border-radius:3px;
	max-width:90%;position: fixed;left:50%;top: 50%;color: #fff;font-size: 1rem;display: none;
	-webkit-transform: translate(-50%, -50%);transform: translate(-50%, -50%);
}

.listBox{margin: 10px 0;}
.listBox li{padding:10px;border-bottom: 1px solid #eee; background: #fff;overflow: hidden;}
.listBox li a{display: flex;display: -webkit-flex; /* Safari */justify-content:space-between;}
.listBox li .leftBox{float: left;}
.listBox li .leftBox .listInfo{color: #999;font-size: 1rem;margin-top: 0.5rem;}
.listBox li .leftBox .listInfo .but1{border:1px solid #eee;margin-right: 5px;color: #ed7655; padding: 1px 2px;border-radius:5px;-webkit-border-radius:5px;}
.listBox li .leftBox .listInfo .but2{}
.listBox li .rightBox{float: right;}
.listBox li .rightBox img{width: 10rem;height: 7rem;}
.listBox li .listTitle{font-size: 1.5rem;color: #333;height: 4rem;}

</style>



