<template>
  <div class="contentHtml">
  	
  	<scroller :on-refresh="refresh"  :on-infinite="infinite" ref="myscroller">
  	  <ul class="listBox">
  	  	<template v-for="listData in listDatas">
	  		<li v-on:click="hrefTo(listData.id)">
	    			<div class="leftBox">
	    				<div class="listTitle">{{listData.title.rendered}}</div>
	    				<div class="listInfo"><span class="but1"></span><span> {{listData.date | timeFormatter}} {{listData.views}}</span></div>
	    			</div>
	    			<div class="rightBox"><img src="../assets/img.png"></div>
	    		</li>
    		 </template>
  		<!--<router-link to="/test">
	    		<li>
	    			<div class="leftBox">
	    				<div class="listTitle"></div>
	    				<div class="listInfo"><span class="but1"></span><span> 0912 13773</span></div>
	    			</div>
	    			<div class="rightBox"><img src="../assets/img.png"></div>
	    		</li>
    		</router-link>-->
      </ul>
   </scroller>
    <div style="display: none;">{{page}}</div>
  </div>
</template>

<script>
$("title").text("")
export default {
  name: 'contentHtml',
  data () {
    return {
    	listDatas:[],
    	page: 1,
    	input:'',
    	message:''
    }
  },
  created:function(){
  	
  },
  methods: {
    getList: function (update) {
    		var self=this;
    		if(update == "update"){self.page = 1;}
    		var page = self.page;
    		var per_page = 8;
	  	$.get('http://121.14.27.27:88/wp-json/wp/v2/posts',{"page":page,"per_page":per_page},function(data){
	  		console.log(data)
	  		if(update == "update"){self.listDatas = [];}
	  		var start = self.listDatas.length;
  			for(var i = 0; i < data.length; i++) {
		      self.listDatas[start+i] = data[i];
		    }
  			self.$refs.myscroller.resize();//
  			self.page = self.page+1;
  			if(page >= 1){
				self.$refs.myscroller.finishInfinite(2);//
			}
			
		})
    },
    infinite(done) {//
  		this.getList();
 	},
	refresh() {//
		this.getList("update");
 	},
	hrefTo:function(id){//ios
	  	var content = {"type":"href","id":id}
	    	var data = JSON.stringify(content);
	    	if(!this.utilIsLocalhost){
			ttf.nslog(data)
	  	}
	}
  }
}

import Vue from 'vue'
Vue.filter('timeFormatter',function (value){
	return value.substring(5,10);
});
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



